// load-scene-with-assets.js
var LoadSceneWithAssets = pc.createScript('loadSceneWithAssets');
LoadSceneWithAssets.attributes.add('sceneName', { type: 'string' });
LoadSceneWithAssets.attributes.add('loadingProgressTextEntity', { type: 'entity' });

// initialize code called once per entity
LoadSceneWithAssets.prototype.initialize = function () {
    this.entity.button.once('click', function () {
        // Find all the assets that have been tagged with the scene name
        var assets = this.app.assets.findByTag(this.sceneName);

        var assetsLoaded = 0;
        var assestTotal = assets.length;

        // Callback when all the assets are loaded
        var onAssetsLoaded = function () {
            loadScene(this.sceneName, { hierarchy: true, settings: true });
        }.bind(this);

        // Callback function when an asset is loaded
        var onAssetReady = function (asset) {
            assetsLoaded += 1;

            console.log(asset.name);

            // Update the progress text
            this.loadingProgressTextEntity.element.text = assetsLoaded.toString() + '/' + assestTotal.toString() + ' loaded';

            // Once we have loaded all the assets
            if (assetsLoaded === assestTotal) {
                onAssetsLoaded();
            }
        }.bind(this);

        // Start loading all the assets
        for (var i = 0; i < assets.length; i++) {
            assets[i].ready(onAssetReady);
            this.app.assets.load(assets[i]);
        }

        if (assets.length === 0) {
            onAssetsLoaded();
        }
    }, this);
};


