// posteffect-lut.js
/*
LookUp Table (LUT) Post Effect

For this post effect, it requires a texture with lookup data.
lookup-original.png - is base, original lookup, which can be modified in photoshop with color filters.
It is a very cheap way to implement sophisticated color editing.

How to create own LUT texture:
1. Download lookup-original.png
2. Make a screenshot of a scene you want to modify coloring of (make sure it has no post effects on)
3. Open that screenshot in photoshop
4. Add layer filters, e.g. Brightness/Contrast, Hue, Color Balance, etc
5. Edit them to make screenshot to look as desired
6. Add lookup-original image in place of screenshot
7. Save as new PNG-24 file
8. Upload to project
9. Uncheck Mipmaps and set Addressing to Clamp
10. Specify on Camera Posteffect script new lookup texture

This script allows swapping between LUT assets on the fly, and will update in realtime
*/

pc.extend(pc, function () {
    var BrightnessContrastEffect = function (graphicsDevice) {
        this.shader = new pc.Shader(graphicsDevice, {
            attributes: {
                aPosition: pc.SEMANTIC_POSITION
            },
            vshader: [
                "attribute vec2 aPosition;",
                "",
                "varying vec2 vUv0;",
                "",
                "void main(void)",
                "{",
                "    gl_Position = vec4(aPosition, 0.0, 1.0);",
                "    vUv0 = (aPosition.xy + 1.0) * 0.5;",
                "}"
            ].join("\n"),
            fshader: [
                "precision " + graphicsDevice.precision + " float;",
                "uniform sampler2D uColorBuffer;",
                "uniform sampler2D uLut;",

                "varying vec2 vUv0;",
                
                "vec4 lookup(in vec4 textureColor, in sampler2D lookupTable) {",
                    "mediump float blueColor = textureColor.b * 63.0;",
                    "mediump vec2 quad1;",
                    "quad1.y = floor(floor(blueColor) / 8.0);",
                    "quad1.x = floor(blueColor) - (quad1.y * 8.0);",
                    "mediump vec2 quad2;",
                    "quad2.y = floor(ceil(blueColor) / 8.0);",
                    "quad2.x = ceil(blueColor) - (quad2.y * 8.0);",
                    "highp vec2 texPos1;",
                    "texPos1.x = (quad1.x * 0.125) + 0.5/512.0 + ((0.125 - 1.0/512.0) * textureColor.r);",
                    "texPos1.y = (quad1.y * 0.125) + 0.5/512.0 + ((0.125 - 1.0/512.0) * textureColor.g);",
                    "texPos1.y = 1.0-texPos1.y;",
                    "highp vec2 texPos2;",
                    "texPos2.x = (quad2.x * 0.125) + 0.5/512.0 + ((0.125 - 1.0/512.0) * textureColor.r);",
                    "texPos2.y = (quad2.y * 0.125) + 0.5/512.0 + ((0.125 - 1.0/512.0) * textureColor.g);",
                    "texPos2.y = 1.0-texPos2.y;",
                    "lowp vec4 newColor1 = texture2D(lookupTable, texPos1);",
                    "lowp vec4 newColor2 = texture2D(lookupTable, texPos2);",
                    "lowp vec4 newColor = mix(newColor1, newColor2, fract(blueColor));",
                    "return newColor;",
                "}",

                "void main() {",
                    "vec4 base = texture2D( uColorBuffer, vUv0 );",
                
                    "gl_FragColor = lookup(base, uLut);",
                "}"
            ].join("\n")
        });
    };

    BrightnessContrastEffect = pc.inherits(BrightnessContrastEffect, pc.PostEffect);

    BrightnessContrastEffect.prototype = pc.extend(BrightnessContrastEffect.prototype, {
        render: function (inputTarget, outputTarget, rect) {
            var device = this.device;
            var scope = device.scope;

            scope.resolve("uColorBuffer").setValue(inputTarget.colorBuffer);
            scope.resolve("uLut").setValue(this.textureLut.resource);
            
            pc.drawFullscreenQuad(device, outputTarget, this.vertexBuffer, this.shader, rect);
        }
    });

    return {
        BrightnessContrastEffect: BrightnessContrastEffect
    };
}());


var BrightnessContrast = pc.createScript('brightnessContrast');

BrightnessContrast.attributes.add('textureLut', {
    type: 'asset',
    assetType: 'texture',
    title: 'LUT Texture'
});

BrightnessContrast.prototype.initialize = function() {
    this.effect = new pc.BrightnessContrastEffect(this.app.graphicsDevice);
    this.effect.textureLut = this.textureLut;
    
    // update LUT asset if changed
    this.on('attr:textureLut', function(value) {
        this.effect.textureLut = value;
    });

    var queue = this.entity.camera.postEffects;

    queue.addEffect(this.effect);

    this.on('state', function (enabled) {
        if (enabled) {
            queue.addEffect(this.effect);
        } else {
            queue.removeEffect(this.effect);
        }
    });

    this.on('destroy', function () {
        queue.removeEffect(this.effect);
    });
};


