// unload-assets-on-click.js
var UnloadAssetsOnClick = pc.createScript('unloadAssetsOnClick');
UnloadAssetsOnClick.attributes.add('assetTag', {type: 'string'});

// initialize code called once per entity
UnloadAssetsOnClick.prototype.initialize = function() {
    this.entity.button.once('click', function() {
        var assets = this.app.assets.findByTag(this.assetTag);
        for (var i = 0; i < assets.length; i++) {
            assets[i].unload();
        }
    }, this);
};

