// shader-chunk.js
var ShaderChunk = pc.createScript('shaderChunk');

ShaderChunk.attributes.add('chunkName', { type: 'string' });
ShaderChunk.attributes.add('shader', { type: 'asset', assetType: 'shader' });

ShaderChunk.prototype.initialize = function() {
    var material = this.entity.model.meshInstances[0].material;

    if (this.shader)
        material.chunks[this.chunkName + 'PS'] = this.shader.resource;

    this.shader.on('load', function() {
        material.chunks[this.chunkName + 'PS'] = this.resource;
        material.update();
    });

    material.update();
};

